import { Platform } from "ionic-angular";
import { Injectable, isDevMode } from "@angular/core";

import { AppGlobals } from "@app/app.globals";
import { AppConstants } from "@app/app.constants";

import { MarsPushNotificationService } from "@services/push-notification.service";
import { MarsCartService } from "@services/cart.service";
import { MarsAuthService } from "@services/auth.service";

declare var Mars;

@Injectable()
export class AppMainPages {

    constructor(private platform: Platform,
        private globals: AppGlobals,
        private notificationService: MarsPushNotificationService,
        private cartService: MarsCartService) { };

    refresh() {
        let pages = [
            // Common pages
            {
                getRoot: () => { return "MerchantsSearchPage"; },
                getRootParams: () => { return {} },
                getTitle: () => { return "Estabelecimentos"; },
                getUrlPath: () => { return "app" },
                getIcon: () => { return "la-compass"; },
                getBadge: () => { return 0; },
                canShow: () => { return (!MarsAuthService.isLoggedIn()) || (MarsAuthService.hasRole("CUSTOMER")); },
                disable: () => { return this.globals.disableNavigation }
            },
            {
                getRoot: () => { return "CartPage"; },
                getRootParams: () => { return {} },
                getTitle: () => { return "Carrinho"; },
                getUrlPath: () => { return "app" },
                getIcon: () => { return "la-shopping-cart"; },
                getBadge: () => { return 0; },
                canShow: () => { return !this.cartService.isEmpty(); },
                disable: () => { return this.globals.disableNavigation }
            },
            /* {
                getRoot: () => { return "AdoptPage"; },
                getRootParams: () => { return {} },
                getTitle: () => { return "Adoção"; },
                getUrlPath: () => { return "app" },
                getIcon: () => { return "paw"; },
                getBadge: () => { return 0; },
                canShow: () => { return (!MarsAuthService.isLoggedIn()) || (MarsAuthService.hasRole("CUSTOMER")) || (MarsAuthService.hasRole("ONG")); },
                disable: () => { return this.globals.disableNavigation }
            },
            {
                getRoot: () => { return "LostAndFoundPage"; },
                getRootParams: () => { return {} },
                getTitle: () => { return "Perdidos e Achados"; },
                getUrlPath: () => { return "app" },
                getIcon: () => { return "pin"; },
                getBadge: () => { return 0; },
                canShow: () => { return (!MarsAuthService.isLoggedIn()) || (MarsAuthService.hasRole("CUSTOMER")) || (MarsAuthService.hasRole("ONG")); },
                disable: () => { return this.globals.disableNavigation }
            }, */
            {
                getRoot: () => { return "OrderHistoryPage"; },
                getRootParams: () => { return {} },
                getTitle: () => { return "Pedidos"; },
                getUrlPath: () => { return "app" },
                getIcon: () => { return "la-clock-o"; },
                getBadge: () => { return this.globals.hasPendingOrders ? 1 : 0; },
                canShow: () => { return MarsAuthService.hasRole("CUSTOMER") },
                disable: () => { return this.globals.disableNavigation }
            },
            {
                getRoot: () => { return "CustomerSettingsPage"; },
                getRootParams: () => { return {} },
                getTitle: () => { return "Configurações"; },
                getUrlPath: () => { return "app" },
                getIcon: () => { return "la-cog"; },
                getBadge: () => { return 0; },
                canShow: () => { return MarsAuthService.hasRole("CUSTOMER") },
                disable: () => { return this.globals.disableNavigation }
            },
            // Admin Pages
            {
                getRoot: () => { return "CatalogueCategoriesPage"; },
                getRootParams: () => { return { category: "root" } },
                getTitle: () => { return "Produtos"; },
                getUrlPath: () => { return "catalogue" },
                getIcon: () => { return "folder"; },
                getBadge: () => { return 0; },
                canShow: () => { return MarsAuthService.hasRole("ADMIN") },
                disable: () => { return this.globals.disableNavigation }
            },
            {
                getRoot: () => { return "CataloguePendingItemsPage"; },
                getRootParams: () => { return {} },
                getTitle: () => { return "Submissões"; },
                getUrlPath: () => { return "pending" },
                getIcon: () => { return "swap"; },
                getBadge: () => { return 0; },
                canShow: () => { return MarsAuthService.hasRole("ADMIN") },
                disable: () => { return this.globals.disableNavigation }
            },
            {
                getRoot: () => { return "UsersListPage"; },
                getRootParams: () => { return {} },
                getTitle: () => { return "Usuários"; },
                getUrlPath: () => { return "users" },
                getIcon: () => { return "people"; },
                getBadge: () => { return 0; },
                canShow: () => { return MarsAuthService.hasRole("ADMIN") },
                disable: () => { return this.globals.disableNavigation }
            },
            {
                getRoot: () => { return "SubscriptionPlansListPage"; },
                getRootParams: () => { return {} },
                getTitle: () => { return "Planos (Assinatura)"; },
                getUrlPath: () => { return "subscription" },
                getIcon: () => { return "refresh"; },
                getBadge: () => { return 0; },
                canShow: () => { return MarsAuthService.hasRole("ADMIN") },
                disable: () => { return this.globals.disableNavigation }
            },
            {
                getRoot: () => { return "AdminSettingsPage"; },
                getRootParams: () => { return {} },
                getTitle: () => { return "Configurações"; },
                getUrlPath: () => { return "settings                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    " },
                getIcon: () => { return "settings"; },
                getBadge: () => { return 0; },
                canShow: () => { return MarsAuthService.hasRole("ADMIN") },
                disable: () => { return this.globals.disableNavigation }
            },
            // Merchant Pages
            {
                getRoot: () => { return "OrdersListPage"; },
                getRootParams: () => { return {} },
                getTitle: () => { return "Pedidos"; },
                getUrlPath: () => { return "app" },
                getIcon: () => { return "la-clipboard"; },
                getBadge: () => { return this.globals.hasPendingOrders ? 1 : 0; },
                canShow: () => { return MarsAuthService.hasRole('MERCHANT') },
                disable: () => { return this.globals.disableNavigation }
            },
            {
                getRoot: () => { return "InventoryCategoriesPage"; },
                getRootParams: () => { return { category: "root" } },
                getTitle: () => { return "Estoque"; },
                getUrlPath: () => { return "app" },
                getIcon: () => { return "la-cubes"; },
                getBadge: () => { return 0; },
                canShow: () => { return MarsAuthService.hasRole('MERCHANT') },
                disable: () => { return this.globals.disableNavigation }
            },
            {
                getRoot: () => { return "PromotionHistoryPage"; },
                getRootParams: () => { return {} },
                getTitle: () => { return "Promoções"; },
                getUrlPath: () => { return "app" },
                getIcon: () => { return "la-tags"; },
                getBadge: () => { return 0; },
                canShow: () => { return MarsAuthService.hasRole('MERCHANT') },
                disable: () => { return this.globals.disableNavigation }
            },
            {
                getRoot: () => { return "OrdersAnalyticsPage"; },
                getRootParams: () => { return {} },
                getTitle: () => { return "Minhas Vendas"; },
                getUrlPath: () => { return "app" },
                getIcon: () => { return "la-bar-chart"; },
                getBadge: () => { return 0; },
                canShow: () => { return MarsAuthService.hasRole('MERCHANT') },
                disable: () => { return this.globals.disableNavigation }
            },
            {
                getRoot: () => { return "MerchantSettingsPage"; },
                getRootParams: () => { return {} },
                getTitle: () => { return "Configurações"; },
                getUrlPath: () => { return "app" },
                getIcon: () => { return "la-cog"; },
                getBadge: () => { return 0; },
                canShow: () => { return MarsAuthService.hasRole('MERCHANT') || MarsAuthService.hasRole('SERVICE_ESTABLISHMENT') },
                disable: () => { return this.globals.disableNavigation }
            },
            // ONG Pages
            {
                getRoot: () => { return "AdoptPage"; },
                getRootParams: () => { return {} },
                getTitle: () => { return "Adoção"; },
                getUrlPath: () => { return "app" },
                getIcon: () => { return "la-paw"; },
                getBadge: () => { return 0; },
                canShow: () => { return (!MarsAuthService.isLoggedIn()) || (MarsAuthService.hasRole("CUSTOMER")) || (MarsAuthService.hasRole("ONG")); },
                disable: () => { return this.globals.disableNavigation }
            },
            {
                getRoot: () => { return "LostAndFoundPage"; },
                getRootParams: () => { return {} },
                getTitle: () => { return "Perdidos e Achados"; },
                getUrlPath: () => { return "app" },
                getIcon: () => { return "pin"; },
                getBadge: () => { return 0; },
                canShow: () => { return (!MarsAuthService.isLoggedIn()) || (MarsAuthService.hasRole("CUSTOMER")) || (MarsAuthService.hasRole("ONG")); },
                disable: () => { return this.globals.disableNavigation }
            },
            {
                getRoot: () => { return "OngSettingsPage"; },
                getRootParams: () => { return {} },
                getTitle: () => { return "Configurações"; },
                getUrlPath: () => { return "app" },
                getIcon: () => { return "settings"; },
                getBadge: () => { return 0; },
                canShow: () => { return MarsAuthService.hasRole('ONG') },
                disable: () => { return this.globals.disableNavigation }
            },
            // Common about page
            {
                getRoot: () => { return "AboutPage"; },
                getRootParams: () => { return {} },
                getTitle: () => { return "Sobre"; },
                getUrlPath: () => { return "app" },
                getIcon: () => { return "la-info-circle"; },
                getBadge: () => { return 0; },
                canShow: () => { return !MarsAuthService.isLoggedIn(); },
                disable: () => { return this.globals.disableNavigation }
            },
        ];

        this.globals.rootTabs = pages;

        return pages;
    }
}


